}
.search-bar input[type=text] {
  border: none;
  padding: 1.5rem 3rem;
  background-color: #e1e1e1;
  flex: 1;
  border-radius: 5rem;
  width: 100%;
}
.search-bar .search-button {
  margin: 0 1rem;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
