.navegacion {
  display: flex;
  flex-wrap: wrap; /* Evita que los elementos bajen de línea */
  background-color: #390708;
  margin-bottom: 2rem;
  overflow: hidden; /* Oculta los elementos que no caben */
  white-space: nowrap; /* Fuerza los elementos a estar en una sola línea */
  position: relative;
}
.navegacion a {
  color: #efebe2;
  padding: 2rem 5rem;
  font-size: 1.8rem;
  text-decoration: none;
  white-space: nowrap;
  transition: background-color 0.3s;
}
.navegacion a:hover {
  background-color: #693334;
}
.navegacion a.active {
  background-color: #693334;
}
.navegacion .toggle-nav {
  display: none;
  position: absolute;
  right: 10px;
  bottom: 10px;
  background: none;
  border: none;
  color: #efebe2;
  font-size: 2rem;
  cursor: pointer;
  margin-right: 1rem;
  margin-bottom: 0.5rem;
}
.navegacion .toggle-nav::before {
  content: "▼";
}
.navegacion.extra .toggle-nav {
  display: block;
}
.navegacion.expanded {
  overflow: visible;
}
.navegacion.expanded .toggle-nav::before {
  content: "▲";
}

